export default{
	data(){
		return{
			news:[]
		}
	},
	methods:{
		
	},
	onLoad() {
		var that=this
		var arr=[]
		uni.getStorage({
			key:'collectList',
			success(e){
				that.news=JSON.parse(e.data)
				console.log(JSON.parse(e.data));
				that.news.forEach((item,index)=>{
					if(item.collect){
						arr.push(item)
					}
				})
				that.news=arr
			},
			fail: function() {
				that.news=[]
			}
		})
	}
}
